<template>
  <div class="slide-view">
    <p class="desc"
       v-show="title.length > 0">{{ title }}</p>

    <scroll :probeType="3"
            :scrollX="true"
            ref="scroll">
      <slot>
        <slide-item v-for="(item, index) in list"
                    :key="index">
          <div slot="image-content">
            <img v-lazy="item.img.replace('w.h', '170.230')"
                 alt="" />
            <h4 class="wish">{{ item.wish }}人想看</h4>
            <span>✦</span>
          </div>

          <div slot="text">
            <p class="name line-ellipsis">{{ item.nm }}</p>
            <p class="date">{{ item.comingTitle }}</p>
          </div>
        </slide-item>
      </slot>
    </scroll>
  </div>
</template>

<script>
//import SlideItem from '@/components/Common/slideView/SlideItem';
import Scroll from '@/components/Common/scroll/Scroll'

import { debounce } from '@/common/utils.js'

export default {
  name: 'SlideView',
  props: {
    list: null,
    title: { type: String, default: '近期最受期待' }
  },
  data () {
    return {}
  },

  components: {
    // SlideItem
    Scroll
  },

  computed: {},

  methods: {
    refresh () {
      this.$refs.scroll.refresh()
    }
  },
  mounted () {
    // 防抖
    const refresh = debounce(this.$refs.scroll.refresh, 400)

    // 所有图片加载完成后，刷新better-scroll的高度
    this.$bus.$on('slideImageLoad', () => {
      refresh()
    });
  }
}
</script>
<style scoped>
.slide-view {
  padding: 8px 10px;
  margin-bottom: 10px;
  background: white;
}

.desc {
  font-size: 14px;
  color: #333;
  margin-bottom: 12px;
}
/** 

.list {
  overflow: scroll;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  padding: 0 10px;
}

.list::-webkit-scrollbar {
  display: none;
}

.content {
  display: inline-block;
}

.wrapper {
  width: 100%;
}
*/
</style>

