<template>
  <div class="movie-page">
    <div class="movie-info"
         :style="{ background: movie.backgroundColor }">
      <div class="crumbs-nav">
        <a href="/">猫眼电影</a>&nbsp;&gt;&nbsp;
        <span>{{ movie.nm }}</span>
      </div>

      <div class="movie-header">
        <div class="movie-img">
          <img :src="movie.img.replace('w.h', '128.180')"
               alt />
        </div>
        <ul class="movie-info-top">
          <li class="movie-cn-name">
            <h1>{{ movie.nm }}</h1>
          </li>
          <li>{{ movie.enm }}</li>
          <li v-if="movie.globalReleased && movie.sc !== 0"
              class="score">
            观众评
            <span>{{ movie.sc }}</span>
          </li>
          <li style="font-size: 14px;color: #888"
              v-if="movie.globalReleased && movie.sc == 0">
            暂无评分
          </li>
          <!-- <li v-if="!movie.globalReleased" class="score">
          <span>{{ movie.wish }}</span> 想看
        </li> -->
          <li>{{ movie.cat }}</li>
          <li>{{ movie.star.replace(/,/g, ' / ') }}</li>
          <li>{{ movie.pubDesc }} / {{ movie.dur }} 分钟</li>

          <li class="btns">
            <button class="button want-to-watch">
              <img class="img noneBg"
                   alt="想看"
                   src="//s0.meituan.net/bs/?f=myfe/canary:/asgard/images/movie/want-to-watch.png" /><span>想看</span>
            </button>
            <button class="button have-watched">
              <img class="img noneBg"
                   alt="看过"
                   src="//s0.meituan.net/bs/?f=myfe/canary:/asgard/images/movie/star.png" /><span>看过</span>
            </button>
          </li>
        </ul>
      </div>

      <div class="real-time-amount">
        <div class="top clear-fix">
          <div class="left">
            <img class="img noneBg"
                 alt="logo"
                 src="//s0.meituan.net/bs/?f=myfe/canary:/asgard/images/movie/logo.png">
            <span>实时口碑</span>
          </div>
        </div>

        <div class="middle">
          <h3>
            <span>{{movie.wish}}</span>
            人想看
          </h3>
        </div>
      </div>

      <!-- <button class="buy-btn">特惠购票</button> -->

      <toggle-view>
        <p class="toggle-paragraph">{{ movie.dra }}</p>
      </toggle-view>

      <toggle-view title="演职人员"
                   :isAllowToggle="false"
                   closeText="全部">

      </toggle-view>

      <toggle-view title="视频剧照"
                   :isAllowToggle="false"
                   closeText="全部剧照">
        <star-list :list="moviePic"></star-list>
      </toggle-view>

    </div>

    <comment-view />
  </div>
  <!-- http://m.maoyan.com/asgard/movie/1230199?&channelId=4&cityId=257&$from=canary# -->
</template>

<script>
import { getMovieDetail } from '@/network/home.js'

import ToggleView from '@/components/Content/ToggleView';
import StarList from '@/components/Content/StarList';
import CommentView from '@/components/Content/CommentView'

export default {
  name: 'MovieDetail',
  props: [],
  data () {
    return {
      movie: {},
      moviePic: []
    }
  },

  components: {
    ToggleView, StarList,
    CommentView
  },

  computed: {},

  methods: {
    getMovieDetail (id) {
      getMovieDetail(id).then(res => {
        this.movie = res.detailMovie
        this.moviePic = this.movie.photos
        // console.log(this.moviePic,res)
      })
    }
  },

  created () {
    this.getMovieDetail(this.$route.params.id)
  }
}
</script>
