<template>
  <div class="toggle-view">
    <div class="header clear-fix">
      <div class="left">
        <span class="title">
          {{ title }}
        </span>
      </div>

      <div class="right"
           @click="changeStatus()"
           :class="[status?openArrowClass:closeArrowClass]">
        <span class="desc">
          {{ toggleText() }}
        </span>
        <img class="img"
             src="//s0.meituan.net/bs/?f=myfe/canary:/asgard/images/movie/arrow-right.png"
             alt="">
      </div>
    </div>

    <div class="content"
         :class="[status?'open-status':'close-status']">
      <slot></slot>
    </div>
  </div>
</template>

<script>

export default {
  name: 'ToggleView',
  props: {
    title:{
      type:String,
      default:"简介"
    },
    openText: {
      type:String,
      default:"收起"
    },
    openArrowClass: {
      type:String,
      default:"up-arr"
    },
    closeText: {
      type:String,
      default:"展开"
    },
    closeArrowClass:{
      type:String,
      default:"down-arr"
    },
    isAllowToggle:{
      type:Boolean,
      default:true
    }
  },
  data () {
    return {
      // 展开状态
      status: false
    };
  },

  components: {},

  computed: {},

  methods: {
    changeStatus () {
      if(this.isAllowToggle){
        this.status = !this.status;
      }
    },
    toggleText () {
      return this.status ? this.openText : this.closeText;
    },
  },
  created(){
    console.log(this.title)
  }
}

</script>
<style scoped>
.toggle-view{
  color: white;
  /* height: 150px; */
  margin-top: 18px;
  font-size: 15px;
}
.header{
  margin-bottom: 10px;
}
.right{
  opacity: 0.6;
  font-size: 11px;
}
.right .img{
  margin-left: 6px;
  width: 5px;
  height: 9px;
}
.down-arr .img {
  transform: rotate(90deg);
}
.up-arr .img {
  transform: rotate(-90deg);
}
.left-arr .img{
  transform: rotate(-180deg)
}
.right-arr .img{
  transform: rotate(0deg)
}
</style>